import React from 'react';
import { FaEllipsisH } from 'react-icons/fa';

import classes from './Ellipsis.module.css';

const ellipsis = (props) => {
    return (
        <div className={classes.change} onClick={props.onClick} >
            <FaEllipsisH />
        </div>
    );
}

export default ellipsis;