import React from 'react';

import module from './Item.module.css';
import OSIcon from '../../../../UI/Icons/OS/OS';
import { awsS3BucketUrl } from '../../../../../shared/utility';

const item = (props) => {
  let src = null;
  if(props.item.photo) {
    src = awsS3BucketUrl + props.item.photo.name;
  }
  let osi = null;
  if(props.item.outOfStock) {
    osi = (
      <div className={module.OSI} >
        <OSIcon />
      </div>
    );
  }
  return (
    <div className={module.Item} onClick={() => props.onItemClick(props.item)} >
      <div className={module.Photo} >
        {/* eslint-disable-next-line */}
        <img src={src} className={module.Img} />
        {osi}
      </div>
      <div className={module.Name} >
        {props.item.name}
      </div>
    </div>
  );
}

export default item;