import React from 'react';

import module from './AllItems.module.css';
import Item from './Item/Item';
import BackIcon from '../../../UI/Icons/Back/Back';
import { getRInput, searchItem } from '../../../../shared/utility';
import Button from '../../../UI/RButton/Button';

const allItems = (props) => {
  let items = (
    <div className={module.Empty} >
      No item Found!
    </div>
  );
  let itms = searchItem(props.items, props.searchitem.value);
  if(itms.length > 0) {
    items = itms.map((item, i) => {
      return (
        <Item 
          key={i}
          item={item}
          onItemClick={props.onItemClick}
        />
      );
    });
  }
  return (
    <div className={module.AllItems} >
      <div className={module.Top} >
        <div className={module.BI} >
          <div className={module.BackIcon} >
            <BackIcon onClick={props.toggleItems} />
          </div>
        </div>
        <div className={module.Cat} >
          {props.category}
        </div>
        <div className={module.AddButton} >
          <Button onClick={props.onIconClick} >Add New Item</Button>
        </div>
        <div className={module.Input} >
          {getRInput(props.searchitem, props.inputChangeHandler)}
        </div>
      </div>
      <div className={module.Items} >
        {items}
      </div>
    </div>
  );
}

export default allItems;